  'use strict';

  angular
    .module('gisproject', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr','leaflet-directive','ngFileUpload']);

