(function() {
  'use strict';

  angular
    .module('gisproject')
    .controller('UploadMapController', UploadMapController);

  /** @ngInject */
  function UploadMapController($scope,$log,mainService) {
    var vm = this;
	
	vm.map;
		function loadFile(file,field)
		{
			mainService.loadFile(file,field).then(function successCallback(response) {
				$log.debug(response);
				vm.map=response.data;
			},function errorCallback(response) { 
				$log.error("Si è verificato un errore");
				$log.debug(response);
				return; 
			});
		}
		
		
		vm.loadFile=loadFile;
	
    }
})();


(function() {
  'use strict';

  angular
    .module('gisproject')
    .controller('ResultController', ResultController);

  /** @ngInject */
  function ResultController($scope,$rootScope,mainService,$stateParams,$http) {
    var vm = this;
	
	
	vm.defaults={
	minZoom: 16
	};
	
	vm.center={ lat:45.494384, lng:9.142647, zoom: 15};
	
	vm.layers={
		baselayers: {
                        xyz: {
                            name: 'OpenStreetMap (XYZ)',
                            url: 'http://tile.openstreetmap.org/{z}/{x}/{y}.png',
                            type: 'xyz'
                        }
                    },
       overlays: {
                     
                        }
	};
	console.log($stateParams.resultLayer);
	var newLayer= {
							name: $stateParams.resultLayer,
                            type: 'wms',
                            visible: true,
                            url: 'http://localhost:8081/geoserver/gisProject/wms',
                            layerParams: {
                                layers: 'gisProject:'+$stateParams.resultLayer,
                                format: 'image/png',
                                transparent: true
                            }
	};
	vm.layers.overlays[$stateParams.resultLayer]=newLayer;
	if ($rootScope.secondLayer)
		vm.layers.overlays['source map']=$rootScope.secondLayer;
	$rootScope.resultLayer=newLayer;
	
	
	vm.statistics;
	
	function getStats()
	{
		mainService.getStats().then(function successCallback(response) {
			vm.statistics=response.data;
			console.log(vm.statistics);
		},function errorCallback(response) { 
			console.log("error");
			return; 
		});
		
	}
	getStats();
	
	function download()
	{
		return "http://localhost:8081/geoserver/gisProject/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=gisProject:"+$rootScope.resultLayer.name+"&outputFormat=SHAPE-ZIP";
    }
	
	vm.download=download;
	
	}
	
})();

(function() {
  'use strict';

  angular
    .module('gisproject')
    .controller('ConfigurationController', ConfigurationController);

  /** @ngInject */
  function ConfigurationController($scope,$log,$rootScope,mainService) {
    var vm = this;
	
	vm.associationName;
	
	vm.class1=[];
	vm.class2=[];
	
	vm.selectionValue=[];
	
	vm.associationList={};
	
	vm.featureList1;
	vm.featureList2;
	
	
	function associate()
	{
		if (vm.associationName==null || vm.associationName==undefined)
			vm.associationName="Default";
		
		vm.associationList[vm.associationName]={};
		vm.associationList[vm.associationName].name=vm.associationName;
		vm.associationList[vm.associationName].classList=[];
		for (var i=0; i<Object.keys(vm.selectionValue).length; i++)
		{
			if (vm.selectionValue[Object.keys(vm.selectionValue)[i]])
			{
				vm.associationList[vm.associationName].classList.push(Object.keys(vm.selectionValue)[i]);
			} 
		}
		
		$rootScope.associationList=vm.associationList;
		console.log($rootScope.associationList);
	
	}
	
	function getFeatures(layerName,featureType,numClass)
	{
		mainService.getGeoJSON(layerName).then(function successCallback(response) {
			console.log(numClass);
				for (var i=0; i<response.data.features.length; i++)
				{
					var value=response.data.features[i].properties[featureType];
					if (numClass==1){
						if (vm.class1.indexOf(value)<0 && value!="")
							vm.class1.push(value);
					}
					else{
						if (vm.class2.indexOf(value)<0 && value!="")
							vm.class2.push(value);
					}
				}
		},function errorCallback(response) { 
			console.log("error");
			return; 
		});
	}
	
	function findProperty(layerName,id)
	{
		mainService.getPropertyList(layerName).then(function successCallback(response) {
		if (id==2)
		{
			vm.featureList2=response.data;
			$log.debug(vm.featureList2);
			}
		else
			vm.featureList1=response.data;
		},function errorCallback(response) { 
			console.log("error");
			return; 
		});
		
	}
	
	findProperty($rootScope.layer1,1);
	findProperty($rootScope.layer2,2);
	
	
	vm.associate=associate;
	vm.getFeatures=getFeatures;
	
    }
})();

(function() { 

angular
.module("gisproject")
.service("MarkerService", MarkerService);
/** @ngInject */
function MarkerService($http,$rootScope)
{


this.send = function(markerObject) {
var markerArray = Object.keys(markerObject).map(function (key) {return markerObject[key]});
var promise= $http.post("http://127.0.0.1:8080/GisProjectServer/map?layer1=OSM_00_4326&layer2=DBT_00_4326",markerArray);
return promise; 
};

this.remove = function() {
var promise= $http["delete"]("http://127.0.0.1:8080/GisProjectServer/map");
return promise; 
};


this.search = function() {

var promise= $http.get("http://127.0.0.1:8080/GisProjectServer/map");
return promise; 
};

this.runAlgorithm= function(parameters,associationList){
	var data={};

	if (parameters!=undefined)
		data.parameters=Object.keys(parameters).map(function (key) {return parameters[key]});
	else
		data.parameters=[];
	
	data.parameters=parameters;


	if (associationList!=undefined)
		data.associationList=Object.keys(associationList).map(function (key) {return associationList[key]});
	else
		data.associationList=[];
	
	if ($rootScope.layer1)
		data.layer1=$rootScope.layer1;
	
	if ($rootScope.layer2)
		data.layer1=$rootScope.layer2;
		
	
		
	
	var promise= $http.post("http://127.0.0.1:8080/GisProjectServer/algorithm",data);
	return promise; 

}

this.getResult= function(markerList){
var markerArray = Object.keys(markerList).map(function (key) {return markerList[key]});
var promise= $http.post("http://127.0.0.1:8080/GisProjectServer/algorithm/result",markerArray);
return promise; 

}

}
})();

(function() {
  'use strict';

  angular
    .module('gisproject')
    .directive('gisNavbar', gisNavbar);

  /** @ngInject */
  function gisNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() { 

angular
.module("gisproject")
.service("mainService", MainService);
/** @ngInject */
function MainService($http)
{
		this.loadFile= function(file,field){
			var formData = new FormData();
			if (file!=null)
				formData.append('file',file);
			var promise= $http.post("http://127.0.0.1:8080/GisProjectServer/mapFile/load"+field+"/",formData,{
				headers: {'Content-Type': undefined}
			});
			return promise; 
		}
		
		this.findAll= function(){
			var promise= $http.post("http://127.0.0.1:8080/GisProjectServer/mapFile/search",{});
			return promise; 
		}
		
		this.getStats= function(){
			var promise= $http.post("http://127.0.0.1:8080/GisProjectServer/algorithm/statistics");
			return promise; 
		}
		
		this.getGeoJSON= function(layerName){
		
		var promise=$http.get("http://localhost:8081/geoserver/gisProject/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=gisProject:"+layerName+"&maxFeatures=100&outputFormat=application/json");
		return promise;
		
		}
		
		this.getPropertyList= function(layerName){
			var promise= $http.get("http://127.0.0.1:8080/GisProjectServer/configuration?layerName="+layerName);
			return promise; 
		}
}
})();


(function() {
  'use strict';

  angular
    .module('gisproject')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope,$rootScope,MarkerService,$log,mainService,$state) {
    var vm = this;
	
	vm.showResult=false;
	vm.markerPairList=[];
	vm.selectedPair;
	
	vm.markers1={};
	vm.markers2={};
	vm.markers1Count=0;
	vm.markers2Count=0;
	//status of the accordion menu on the right
	vm.status = {
		isCustomHeaderOpen: false,
		isFirstOpen: false,
		isFirstDisabled: false
	};
	
	// definition of the custom icon
	vm.customIcon={
        iconUrl: 'assets/images/end_point.png',
        iconSize:     [15, 15], 
    };
	//function to show the selected pair
	function selectPair(e)
	{
		var item=Number($(".leaflet-contextmenu-item").first().text().replace("Remove pair #",""));
		$(".leaflet-contextmenu-item").remove();
		vm.selectedPair=vm.markerPairList[item-1];
		vm.status.open = true;
	}
	//remove marker 1
	function removeMarker1(e)
	{
		var item=Number($(".leaflet-contextmenu-item").text().replace("Remove pair #",""));
		$(".leaflet-contextmenu-item").remove();
		delete vm.markers1[(item)];
		delete vm.markerPairList[item-1].marker1;
		if (vm.markerPairList[item-1].marker2==null)
			vm.markerPairList.splice(item-1,1);
		vm.markers1Count--;
	}
	//remove marker 2
	function removeMarker2(e)
	{
		var item=Number($(".leaflet-contextmenu-item").text().replace("Remove pair #",""));
		$(".leaflet-contextmenu-item").remove();
		delete vm.markers2[(item)];
		delete vm.markerPairList[item-1].marker2;
		if (vm.markerPairList[item-1].marker1==null)
			vm.markerPairList.splice(item-1,1);
		vm.markers2Count--;
	}
	//retrieve marer list from the server and show them
	function searchMarker()
	{
		MarkerService.search().then(function successCallback(response) {
				vm.markerPairList=response.data;
				for (var i =0; i<vm.markerPairList.length; i++)
				{
					manageMarkerPairItem(i);
				}
				vm.markers1Count=vm.markerPairList.length;
				vm.markers2Count=vm.markerPairList.length;
			},function errorCallback(response) { 
				console.log("error");
				return; 
			});
	}
	
	searchMarker();
	
	vm.defaults1={
		minZoom: 15
		
	};
	//define the managed events of the maps
	vm.events1= {
            map: {
                enable: ['drag', 'click','contextmenu.select'],
                logic: 'emit'
            }
        };
	
	
	
	vm.wmsUrl;
	vm.layerName;
	vm.layer;
	vm.center1={ lat:45.494384, lng:9.142647, zoom: 18};
	vm.center2={ lat:45.494384, lng:9.142647, zoom: 18};
	vm.centerLat;
	vm.centerLng;
	
	//base layers
	vm.layers1={
		baselayers: {
                        xyz: {
                            name: 'OpenStreetMap (XYZ)',
                            url: 'http://tile.openstreetmap.org/{z}/{x}/{y}.png',
                            type: 'xyz'
                        }
                    },
       overlays: {}
	};
	
		vm.layers2={
		baselayers: {
                        xyz: {
                            name: 'OpenStreetMap (XYZ)',
                            url: 'http://tile.openstreetmap.org/{z}/{x}/{y}.png',
                            type: 'xyz'
                        }
                    },
       overlays: {}
	};	

	//custom behaviour on click
	$scope.$on('leafletDirectiveMap.map1.click', function(event, args){
		vm.addMarker1(args.leafletEvent.latlng.lat,args.leafletEvent.latlng.lng);
	});
	
	$scope.$on('leafletDirectiveMap.map2.click', function(event, args){
		vm.addMarker2(args.leafletEvent.latlng.lat,args.leafletEvent.latlng.lng);
	});
	
	//add marker to the first map
	function addMarker1(latitude,longitude)
	{
		vm.markers1Count++;
		
		var newMarker={
			lat:latitude,
			lng:longitude,
			focus: true,
			message: 'pair #'+vm.markers1Count,
			draggable: true,
			icon : vm.customIcon,
			contextmenu: true,
			contextmenuWidth: 140,
			contextmenuItems: [{
				text: 'Remove pair #'+vm.markers1Count,
				icon: 'http://files.softicons.com/download/toolbar-icons/16x16-free-application-icons-by-aha-soft/ico/Application.ico',
				iconCls: 'Test',
				hideOnSelect: false,
				callback: function() { vm.removeMarker1()}
			},{
							text: 'Select pair #'+vm.markers1Count,
							icon: 'http://files.softicons.com/download/toolbar-icons/16x16-free-application-icons-by-aha-soft/ico/Application.ico',
							iconCls: 'Test',
							hideOnSelect: false,
							callback: function() { vm.selectPair()}
						}]
		};
		
		if (!vm.markerPairList[vm.markers1Count-1])
		{
			vm.markerPairList.push({});
			vm.markerPairList[vm.markers1Count-1].markerPairId=vm.markers1Count;
		}
		
		vm.markerPairList[vm.markers1Count-1].marker1=newMarker;
		vm.markers1[vm.markers1Count]=newMarker;
	}
	//add marker to the second map
	function addMarker2(latitude,longitude)
	{
		vm.markers2Count++;
		
		var newMarker={
			lat:latitude,
			lng:longitude,
			focus: true,
			message: 'pair #'+vm.markers2Count,
			draggable: true,
			icon : vm.customIcon,
			contextmenu: true,
			contextmenuWidth: 140,
			contextmenuItems: [{
				text: 'Remove pair #'+vm.markers2Count,
				icon: 'http://files.softicons.com/download/toolbar-icons/16x16-free-application-icons-by-aha-soft/ico/Application.ico',
				iconCls: 'Test',
				hideOnSelect: false,
				callback: function() { vm.removeMarker2()}
			},{
							text: 'Select pair #'+vm.markers2Count,
							icon: 'http://files.softicons.com/download/toolbar-icons/16x16-free-application-icons-by-aha-soft/ico/Application.ico',
							iconCls: 'Test',
							hideOnSelect: false,
							callback: function() { vm.selectPair()}
						}]
		};
		
		
		if (!vm.markerPairList[vm.markers2Count-1])
		{
			vm.markerPairList.push({});
			vm.markerPairList[vm.markers2Count-1].markerPairId=vm.markers2Count;
		}
		
		vm.markerPairList[vm.markers2Count-1].marker2=newMarker;
		vm.markers2[vm.markers2Count]=newMarker;
	}
	
	// function not used after removal of the marker list
	function highLightMarker(index)
	{
		vm.markers1[index].focus=true;
		vm.markers2[index].focus=true;
		$log.debug(vm.markers1);
		$log.debug(index);
	}
	
	//send markers to the server that store them
	function sendMarker()
	{
		MarkerService.send(vm.markerPairList).then(function successCallback(response) {
				
				vm.markerPairList=response.data;
				for (var i =0; i<vm.markerPairList.length; i++)
				{
					manageMarkerPairItem(i);
				}
				vm.markers1Count=vm.markerPairList.length;
				vm.markers2Count=vm.markerPairList.length;
			},function errorCallback(response) { 
				console.log("error");
				return; 
			});
	}
	
	//remove the markers from the map and delete them from database
	function removeMarker()
	{
		MarkerService.remove().then(function successCallback(response) {
				vm.markerPairList=[];
				vm.markers1={};
				vm.markers2={};
				vm.markers1Count=0;
				vm.markers2Count=0;
			},function errorCallback(response) { 
				console.log("error");
				return; 
		});
	}
	
	//add layer
	function addLayer(mapIndex)
	{
		var newLayer;
		
		if (vm.selectedMapFile==null || vm.selectedMapFile==null)
		{
			
			newLayer= {
							name: vm.layerName,
                            type: 'wms',
                            visible: true,
                            url: vm.wmsUrl,
                            layerParams: {
                                layers: vm.layer,
                                format: 'image/png',
                                transparent: true
                            }
			
			};
		
		} else
		{
				newLayer= {
							name: vm.selectedMapFile.name,
                            type: 'wms',
                            visible: true,
                            url: 'http://localhost:8081/geoserver/gisProject/wms',
                            layerParams: {
                                layers: vm.selectedMapFile.work_space+':'+vm.selectedMapFile.name,
                                format: 'image/png',
                                transparent: true
                            }
			
			};
			
		}
		
		
		if (mapIndex==1)
		{
			vm.layers1.overlays[vm.layerName]=newLayer;
			$rootScope.layer1=newLayer.name;
			$rootScope.firstLayer=newLayer;
			}
		else
		{
			$rootScope.layer2=newLayer.name;
			vm.layers2.overlays[vm.layerName]=newLayer;
			$rootScope.secondLayer=newLayer;
		}
	
	}
	//function to center map in a certain point
	function center(center)
	{
		center.lat=Number(vm.centerLat);
		center.lng=Number(vm.centerLng);
		$log.debug(center);
	
	}
	
	vm.mapFileList;
	vm.selectedMapFile;
	
	//search for the saved map
	function searchMapFile()
	{
		mainService.findAll().then(function successCallback(response) {
			vm.mapFileList=response.data;
			// set session saved layers
			if ($rootScope.firstLayer)
				vm.layers1.overlays[vm.layerName]=$rootScope.firstLayer;
			if ($rootScope.secondLayer)
				vm.layers2.overlays[vm.layerName]=$rootScope.secondLayer;
			
		},function errorCallback(response) { 
			$log.error("Si è verificato un errore");
			$log.debug(response);
			return; 
		});
	}
	// run algorithm and obtain homologous points
	function runAlgorithm()
	{
		MarkerService.send(vm.markerPairList).then(function successCallback(response) {
							MarkerService.runAlgorithm($rootScope.parameters,$rootScope.associationList).then(function successCallback(response) {
								vm.markerPairList=response.data;
								for (var i =0; i<vm.markerPairList.length; i++)
								{
									manageMarkerPairItem(i);
								}
				                vm.markers1Count=vm.markerPairList.length;
								vm.markers2Count=vm.markerPairList.length;
								vm.showResult=true;
							},function errorCallback(response) { 
								console.log("error");	
								return; 
							});			
				},function errorCallback(response) { 
							console.log("error");
							return; 
				});
	}
	
	// manage a pair by adding two markers on the two maps
	function manageMarkerPairItem(i)
	{
			var test=vm.markerPairList[i].marker1.message;
					vm.markers1[(i+1)]=vm.markerPairList[i].marker1;
					vm.markers2[(i+1)]=vm.markerPairList[i].marker2;
					vm.markers1[(i+1)].focus=false;
					vm.markers1[(i+1)].draggable=true;
					vm.markers2[(i+1)].focus=false;
					vm.markers2[(i+1)].draggable=true;
					
					vm.markers1[(i+1)].icon=vm.customIcon;
					vm.markers2[(i+1)].icon=vm.customIcon;
					
					 vm.markers1[(i+1)].contextmenu=true;
					 vm.markers1[(i+1)].contextmenuWidth=140;
					 
					vm.markers1[(i+1)].contextmenuItems=[{
							text: 'Remove pair #'+(i+1),
							icon: 'http://files.softicons.com/download/toolbar-icons/16x16-free-application-icons-by-aha-soft/ico/Application.ico',
							iconCls: 'Test',
							hideOnSelect: false,
							callback: function() { vm.removeMarker1()}
						},{
							text: 'Select pair #'+(i+1),
							icon: 'http://files.softicons.com/download/toolbar-icons/16x16-free-application-icons-by-aha-soft/ico/Application.ico',
							iconCls: 'Test',
							hideOnSelect: false,
							callback: function() { vm.selectPair()}
						}];
					vm.markers2[(i+1)].contextmenu=true;
					vm.markers2[(i+1)].contextmenuWidth=140;
					vm.markers2[(i+1)].contextmenuItems=[{
							text: 'Remove pair #'+(i+1),
							icon: 'http://files.softicons.com/download/toolbar-icons/16x16-free-application-icons-by-aha-soft/ico/Application.ico',
							iconCls: 'Test',
							hideOnSelect: false,
							callback: function() { vm.removeMarker2()}
						},{
							text: 'Select pair #'+(i+1),
							icon: 'http://files.softicons.com/download/toolbar-icons/16x16-free-application-icons-by-aha-soft/ico/Application.ico',
							iconCls: 'Test',
							hideOnSelect: false,
							callback: function() { vm.selectPair()}
						}];
	}
	
	
	
	//obtain result and redirect to the result view
	function getResult(){
		MarkerService.getResult(vm.markerPairList).then(function successCallback(response) {
				$state.go('result',{resultLayer: response.data});
		},function errorCallback(response) { 
		console.log(response);
			console.log("error");
			return; 
		});
	}
	
	
	searchMapFile();
	
	vm.selectPair=selectPair;
	vm.addMarker1=addMarker1;
	vm.addMarker2=addMarker2;
	vm.highLightMarker=highLightMarker;
	vm.sendMarker=sendMarker;
	vm.addLayer=addLayer;
	vm.center=center;
	vm.searchMapFile=searchMapFile;
	vm.removeMarker1=removeMarker1;
	vm.removeMarker2=removeMarker2;
	vm.removeMarker=removeMarker;
	vm.runAlgorithm=runAlgorithm;
	vm.getResult=getResult;
	
    }
})();

(function() {
  'use strict';

  angular
    .module('gisproject')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log,$rootScope) {

		
	$rootScope.parameters={};
  
    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('gisproject')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/home',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
	  .state('uploadMap', {
        url: '/uploadMap',
        templateUrl: 'app/controller/uploadMap/uploadMap.html',
        controller: 'UploadMapController',
        controllerAs: 'vm'
      })
	  .state('result', {
        url: '/result/:resultLayer',
        templateUrl: 'app/controller/result/result.html',
        controller: 'ResultController',
        controllerAs: 'vm'
      })
	  .state('configuration', {
        url: '/configuration',
        templateUrl: 'app/controller/configuration/configuration.html',
        controller: 'ConfigurationController',
        controllerAs: 'vm'
      });

    $urlRouterProvider.otherwise('/home');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('gisproject')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('gisproject')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

